import React,{Component} from 'react';
import {NavLink} from 'react-router-dom';
import {Menu, Dropdown,Icon } from 'antd';
import { Carousel } from 'antd';
import { Anchor } from 'antd';
import home from '../css/Home.module.css'
import * as api from "../api/products.js";
import * as API from "../api/user.js";
import '../css/reset.css'
import '../css/common.css'
export default class Home extends Component{

constructor(props){
	super(props)
	this.onScroll = this.handleScroll.bind(this)
	this.state={
		productslist:[],
		youdianlist:[],
		flowerlist:[],
		dresslist:[],
		shipinlist:[],
		shoelist:[],
		mobilelist:[],
		booklist:[],
		targetOffset: undefined,
		token:localStorage.getItem("token"),
		user:[],
	}
}

componentDidMount(){
	this.setState({
      targetOffset: window.innerHeight / 2,
    });
	api.getProducts({per:5}).then((data)=>{
		this.setState({productslist:data.data.products})
	})
	api.getProducts({per:4,name:""}).then((data)=>{
		this.setState({youdianlist:data.data.products})
	})
	api.getProducts({per:8,name:""}).then((data)=>{
		this.setState({flowerlist:data.data.products})
	})
	api.getProducts({per:8,name:""}).then((data)=>{
		this.setState({dresslist:data.data.products})
	})
	api.getProducts({per:8,name:""}).then((data)=>{
		this.setState({shipinlist:data.data.products})
	})
	api.getProducts({per:8,name:""}).then((data)=>{
		this.setState({shoelist:data.data.products})
	})
	api.getProducts({per:8,name:""}).then((data)=>{
		this.setState({mobilelist:data.data.products})
	})
	api.getProducts({per:8,name:""}).then((data)=>{
		this.setState({booklist:data.data.products})
	})
	API.usermsg(localStorage.getItem("token")).then((data)=>{
		this.setState({user:data.data.userName})
	}).catch((data)=>{
		this.setState({user:'  '})
	})
	window.addEventListener('scroll', this.onScroll)
	
	if(localStorage.getItem('token')){
		document.getElementsByClassName('user')[0].style.display = 'none';
		document.getElementsByClassName('user')[1].style.display = 'block';
	}else{
		document.getElementsByClassName('user')[0].style.display = 'block';
		document.getElementsByClassName('user')[1].style.display = 'none';
	}
}
onClick=()=>{
	let value=document.getElementsByClassName('ipt')[0].value
	this.props.history.push('/search',value)
}
handleScroll = e => {
	if(e.srcElement.scrollingElement.scrollTop>600){
		document.getElementsByClassName("ant-anchor")[0].style.display='block'
	}else{
		document.getElementsByClassName("ant-anchor")[0].style.display='none'
	}
}
componentWillUnmount() {
	//this
    window.removeEventListener('scroll', this.onScroll) 
}

render(){
	const menu1=(
		<Menu>
	    <Menu.Item>
	      <NavLink to=""></NavLink>
	    </Menu.Item>
	    <Menu.Item>
	     <NavLink to=""></NavLink>
	    </Menu.Item>
	  </Menu>
	)
	const { Link } = Anchor;
return(
	<div>
	<header>
		<div className={home.head_content} id="">
			{	
				<div>
				<div className="user">
					<span>, 
						<span>{this.state.user}</span>
					</span>
					<NavLink to="/home">  </NavLink>
					<NavLink to="/login">[]</NavLink>
					<NavLink to="/register">[]</NavLink>
				</div>
				<div className="user">
					<span>, 
						<span>{this.state.user}</span>
					</span>
					<NavLink to="/home">  </NavLink>
					<NavLink to="/home" onClick={()=>{localStorage.removeItem('token');window.location.reload()}}>[]</NavLink>
				</div>
				</div>
			}
			

			<ul>
				<li>
					<Icon type="mobile" />
					<NavLink to="https://www.dbmall.com/wap/"></NavLink>
				</li>
				<li>/</li>
				<li>
					<NavLink to="/mymall"></NavLink>
				</li>	
				<li>/</li>
				<li>
					<NavLink to=""></NavLink>
				</li>	
				<li>/</li>
				<li>
					<Dropdown overlay={menu1}>
					<NavLink to=""><Icon type="caret-down" /></NavLink>
				  </Dropdown>
				</li>	
				<li>/</li>
				<li>
					<NavLink to=""><Icon type="caret-down" /></NavLink>
					
				</li>	
			</ul>
		</div>
		<div className={home.logo}>
			<div className={home.logo_content}>
				<img src='../image/logo_01.png' alt=''/>
				<img src='../image/logo_02.png' alt=''/>
				<div className={home.search}>
					<input placeholder="" className='ipt'/>
					<a className={home.search_btn} onClick={this.onClick}></a>
				</div>
				<NavLink className={home.mycart} to="/cart">
				
				</NavLink>
				<NavLink className={home.mymall} to="/mymall">
				
				</NavLink>
				
			</div>
		</div>
		<div className={home.menu}>
			<div className={home.menu_content}>
				<ul>
					<li>
						<NavLink to=""><Icon type="caret-down" /></NavLink>
					</li>
					<li>
						<NavLink to="/book"></NavLink>
					</li>
					<li>
						<NavLink to="/phone"></NavLink>
					</li>
					<li>
						<NavLink to="/flower"></NavLink>
					</li>
					<li>
						<NavLink to="/eat"></NavLink>
					</li>
					<li>
						<NavLink to="/dress"></NavLink>
					</li>
					<li>
						<NavLink to="/jewelry"></NavLink>
					</li>
				</ul>
			</div>
		</div>
	</header>
		<div className={home.banner}>
		<Carousel autoplay effect="fade">
			<div>
			  <img src='../image/banner1.jpg' alt=''/>
			</div>
			<div>
			  <img src='../image/banner2.jpg' alt=''/>
			</div>
			<div>
			<img src='../image/banner3.jpg' alt=''/>
			</div>
			<div>
			<img src='../image/banner4.jpg' alt=''/>
			</div>
			<div>
			<img src='../image/banner5.jpg' alt=''/>
			</div>
			<div>
			<img src='../image/banner6.jpg' alt=''/>
			</div>
		  </Carousel>
		</div>
		
		<div className={home.jingxuan}>
		<div className={home.jingxuan_content}>
			<div className={home.timg}>
			<img src='../image/logo_03.png' alt=''/>
			</div>
			<div className={home.pditem}>
			{
				this.state.productslist.map((item,i)=>{
					return(
						<div key={i} className={home.jxit}>
							<NavLink to={'/detail/'+item._id}>
							<img src={item.coverImg} className={home.jxitem} alt=''/>
							<p>{item.name}</p>
							<p className={home.hot}>:{item.price}</p>
							</NavLink>
						</div>
					)
				})
			}
			</div>
		</div>
		</div>
		
		<div className={home.products}>
		<div className={home.products_content}>
				<img src='../image/before.png' className={home.before} alt=''/>
				<h2 id=""></h2>
				<div>
					<div className={home.youdianitem}>
						{
							this.state.youdianlist.map((item,i)=>{
								return(
									<div key={i} className={home.yditem}>
										<NavLink to={'/detail/'+item._id}>
										<img src={item.coverImg} className={home.ydimg} alt=''/>
										<p>{item.name}</p>
										<span className={home.hot}>:{item.price}</span>
										</NavLink>
									</div>
								)
							})
						}
					</div>
				</div>
			
			
			
			<img src='../image/before.png' className={home.before} alt=''/>
			<h2 id=""></h2>
			<div>
			<div className={home.floweritem}>
				{
					this.state.flowerlist.map((item,i)=>{
						return(
							<div key={i} className={home.fitem}>
								<NavLink to={'/detail/'+item._id}>
								<img src={item.coverImg} className={home.fimg} alt=''/>
								<p>{item.name}</p>
								<p>{item.descriptions}<span className={home.hot}>:{item.price}</span></p>
								
								</NavLink>
							</div>
						)
					})
				}
			</div>
			</div>
			
			<img src='../image/before.png' className={home.before} alt=''/>
			<h2 id=""></h2>
			<div>
			<div className={home.floweritem}>
				{
					this.state.dresslist.map((item,i)=>{
						return(
							<div key={i} className={home.fitem}>
								<NavLink to={'/detail/'+item._id}>
								<img src={item.coverImg} className={home.fimg} alt=''/>
								<p>{item.descriptions}</p>
								<span className={home.hot}>:{item.price}</span>
								</NavLink>
							</div>
						)
					})
				}
			</div>
			</div>
			
			<img src='../image/before.png' className={home.before} alt=''/>
			<h2 id=""></h2>
			<div>
			<div className={home.floweritem}>
				{
					this.state.shipinlist.map((item,i)=>{
						return(
							<div key={i} className={home.fitem}>
								<NavLink to={'/detail/'+item._id}>
								<img src={item.coverImg} className={home.fimg} alt=''/>
								<p>{item.name}</p>
								<p>{item.descriptions}<span className={home.hot}>:{item.price}</span></p>
								</NavLink>
							</div>
						)
					})
				}
			</div>
			</div>
			
			<img src='../image/before.png' className={home.before} alt=''/>
			<h2 id=""></h2>
			<div>
			<div className={home.floweritem}>
				{
					this.state.shoelist.map((item,i)=>{
						return(
							<div key={i} className={home.fitem}>
								<NavLink to={'/detail/'+item._id}>
								<img src={item.coverImg} className={home.fimg} alt=''/>
								<p>{item.name}</p>
								<p>{item.descriptions}<span className={home.hot}>:{item.price}</span></p>
								</NavLink>
							</div>
						)
					})
				}
			</div>
			</div>
			
			<img src='../image/before.png' className={home.before} alt=''/>
			<h2 id=""></h2>
			<div>
			<div className={home.floweritem}>
				{
					this.state.mobilelist.map((item,i)=>{
						return(
							<div key={i} className={home.fitem}>
								<NavLink to={'/detail/'+item._id}>
								<img src={item.coverImg} className={home.fimg} alt=''/>
								<p>{item.name}<span className={home.hot}>:{item.price}</span></p>
								</NavLink>
							</div>
						)
					})
				}
			</div>
			</div>
			
			<img src='../image/before.png' className={home.before} alt=''/>
			<h2 id=""></h2>
			<div>
			<div className={home.floweritem}>
				{
					this.state.booklist.map((item,i)=>{
						return(
							<div key={i} className={home.bitem}>
								<NavLink to={'/detail/'+item._id}>
								<img src={item.coverImg} className={home.bimg} alt=''/>
								<p>{item.descriptions}</p>
								<span className={home.hot}>:{item.price}</span>
								</NavLink>
							</div>
						)
					})
				}
			</div>
			</div>
			
		</div>
		</div>
		
		<Anchor id="anchor"  targetOffset={this.state.targetOffset}>
			<Link href="#" title="" />
			<Link href="#" title="" />
			<Link href="#" title="" />
			<Link href="#" title="" />
			<Link href="#" title="" />
			<Link href="#" title="" />
			<Link href="#" title="" />
			<Link href="#" title="" />
		</Anchor>
		
		<div className={home.fixed}>
		<div className={home.phone}>
		</div>
		<div className={home.wx}>
		</div>
		</div>
	
	</div>
)
}
}