import React, { Component } from 'react';
import {BrowserRouter as Router,Route,Switch,Redirect} from 'react-router-dom';
import '../css/reset.css'
import '../css/common.css'
import cart from '../css/Cart.module.css'
import mymall from '../css/Mymall.module.css'
import home from '../css/Home.module.css'
import order from '../css/Order.module.css'
import Order from './Order'
import Address from './Address'
import { NavLink } from 'react-router-dom';
import { Button, Icon, Modal } from 'antd';
import * as api from '../api/cart';
import * as API from "../api/user.js";
const { confirm } = Modal;

export default class Mymall extends Component {
    constructor(props) {
        super(props);
        this.state = {
            user: '',
        }

    }
   
    
   
    

    componentDidMount() {
        
        // 
        API.usermsg(localStorage.getItem("token")).then((data)=>{
			this.setState({user:data.data.userName})
		}).catch((data)=>{
			this.setState({user:'  '})
		})
		// 
        if (localStorage.getItem('token')) {
            document.getElementsByClassName('user')[0].style.display = 'none';
            document.getElementsByClassName('user')[1].style.display = 'block';
        } else {
            document.getElementsByClassName('user')[0].style.display = 'block';
            document.getElementsByClassName('user')[1].style.display = 'none';
        }
		
		
	}
    render() {
       

        

        return (
            <div>
                <header>
                    <div className={cart.head_content} id="">
                        {
                            <div>
                                <div className="user">
                                    <span>,
						<span>{this.state.user}</span>
                                        </span>
                                    <NavLink to="/home">  </NavLink>
                                    <NavLink to="/login">[]</NavLink>
                                    <NavLink to="/register">[]</NavLink>
                                </div>
                                <div className="user">
                                    <span>,
						<span>{this.state.user}</span>
                                        </span>
                                    <NavLink to="/home">  </NavLink>
                                    <NavLink to="/home" onClick={() => { localStorage.removeItem('token'); window.location.reload() }}>[]</NavLink>
                                </div>
                            </div>
                        }
                        <ul>
                            <li>
                                <Icon type="mobile" />
                                <NavLink to="https://www.dbmall.com/wap/"></NavLink>
                            </li>
                            <li>/</li>
                            <li>
                                <NavLink to="/order"></NavLink>
                            </li>
                            <li>/</li>
                            <li>
                                <NavLink to=""></NavLink>
                            </li>
                            <li>/</li>
                            <li>
                                <NavLink to=""><Icon type="caret-down" /></NavLink>

                            </li>
                        </ul>
                    </div>
                    <div className={cart.logo}>
                        <div className={cart.logo_content}>
                            <img src='../image/logo_01.png' alt='' />
                            <img src='../image/logo_02.png' alt='' />
							<NavLink className={mymall.mycart} to="/cart">
							
							</NavLink>
							<NavLink className={mymall.mymall} to="/mymall">
							
							</NavLink>
                        </div>
                    </div>

                </header>
                <Router>
                <div className={cart.container}>
                    <div className="content">
                        <div className={mymall.sidebarbox}>
							<div className={mymall.sidebartop}>
								<p></p>
							</div>
                            <ul>
								<li>
									<span> <Icon type="caret-up" /></span>
									<ul>
										<li><NavLink to="/order"></NavLink></li>
										<li><NavLink to="/cart"></NavLink></li>
									</ul>
								</li>
								<li>
									<span> <Icon type="caret-up" /></span>
									<ul>
										<li></li>
										<li></li>
										<li></li>
										<li><NavLink to="/address"></NavLink></li>
										<li></li>
										<li></li>
										<li></li>
									</ul>
								</li>
								<li>
									<span> <Icon type="caret-up" /></span>
									<ul>
										<li></li>
										<li></li>
										<li></li>
									</ul>
								</li>
                            </ul>
                        </div>
						
						
							<div>
                                <Switch>
                                    <Route path="/order" component={Order}></Route>
                                    <Route path="/address" component={Address}></Route>
								    <Redirect to="/order"/>
                                </Switch>
							</div>
						
                    </div>
                </div>
                </Router>

            </div>
        )
    }
}