import React,{Component} from 'react';
import {NavLink} from 'react-router-dom';
import {Menu, Dropdown,Icon,InputNumber,Modal} from 'antd';
import * as api from "../api/products.js";
import * as API from "../api/user.js";
import * as Api from "../api/cart.js";
import '../css/reset.css'
import '../css/common.css'
import home from '../css/Home.module.css'

export default class Detail extends Component{
constructor(props){
	super(props)
	this.state={
		token:localStorage.getItem("token"),
		user:[],
		id:'',
		location:'',
		img:'',
		descriptions:'',
		price:'',
		created:'',
		count:1,
		totalprice:0,
		loading: false,
		visible: false,
	}
}
componentDidMount(){
	document.body.scrollTop = document.documentElement.scrollTop = 0
	API.usermsg(localStorage.getItem("token")).then((data)=>{
		this.setState({user:data.data.userName})
	}).catch((data)=>{
		this.setState({user:'  '})
	})
	if(localStorage.getItem('token')){
		document.getElementsByClassName('user')[0].style.display = 'none';
		document.getElementsByClassName('user')[1].style.display = 'block';
	}else{
		document.getElementsByClassName('user')[0].style.display = 'block';
		document.getElementsByClassName('user')[1].style.display = 'none';
	}
	api.getProductMsg(this.props.match.params.id).then((data)=>{
		this.setState({location:data.data.name,img:data.data.coverImg,descriptions:data.data.descriptions,price:data.data.price,created:data.data.createdAt,totalprice:data.data.price,id:data.data._id})
	})
}
onChange=(value)=>{
	let totalprice=value*this.state.price
	totalprice=totalprice.toFixed(1)
	this.setState({count:value,totalprice:totalprice})
}
addCart=()=>{
	Api.addCart({product:this.props.match.params.id,quantity:this.state.count},localStorage.getItem('token')).then((data)=>{
		Modal.success({
			title: ''+this.state.count+'',
		  });
	}).catch((data)=>{
		Modal.error({
			title: '',
		  });
	})
}
onClick=()=>{
	let value=document.getElementsByClassName('ipt')[0].value
	this.props.history.push('/search',value)
}
render(){
	const menu1=(
		<Menu>
	    <Menu.Item>
	      <NavLink to=""></NavLink>
	    </Menu.Item>
	    <Menu.Item>
	     <NavLink to=""></NavLink>
	    </Menu.Item>
	  </Menu>
	)
return(
	<div>
	<header>
		<div className={home.head_content} id="">
			{	
				<div>
				<div className="user">
					<span>, 
						<span>{this.state.user}</span>
					</span>
					<NavLink to="/home">  </NavLink>
					<NavLink to="/login">[]</NavLink>
					<NavLink to="/register">[]</NavLink>
				</div>
				<div className="user">
					<span>, 
						<span>{this.state.user}</span>
					</span>
					<NavLink to="/home">  </NavLink>
					<NavLink to="/home" onClick={()=>{localStorage.removeItem('token');window.location.reload()}}>[]</NavLink>
				</div>
				</div>
			}
			
	
			<ul>
				<li>
					<Icon type="mobile" />
					<NavLink to="https://www.dbmall.com/wap/"></NavLink>
				</li>
				<li>/</li>
				<li>
					<NavLink to="/mymall"></NavLink>
				</li>	
				<li>/</li>
				<li>
					<NavLink to=""></NavLink>
				</li>	
				<li>/</li>
				<li>
					<Dropdown overlay={menu1}>
					<NavLink to=""><Icon type="caret-down" /></NavLink>
				  </Dropdown>
				</li>	
				<li>/</li>
				<li>
					<NavLink to=""><Icon type="caret-down" /></NavLink>
					
				</li>	
			</ul>
		</div>
		<div className={home.logo}>
			<div className={home.logo_content}>
				<img src='../image/logo_01.png' alt=''/>
				<img src='../image/logo_02.png' alt=''/>
				<div className={home.search}>
					<input placeholder="" className='ipt'/>
					<a className={home.search_btn} onClick={this.onClick}></a>
				</div>
				<NavLink className={home.mycart} to="/cart">
				
				</NavLink>
				<NavLink className={home.mymall} to="/mymall">
				
				</NavLink>
				
			</div>
		</div>
		<div className={home.menu}>
			<div className={home.menu_content}>
				<ul>
					<li>
						<NavLink to=""><Icon type="caret-down" /></NavLink>
					</li>
					<li>
						<NavLink to="/book"></NavLink>
					</li>
					<li>
						<NavLink to="/phone"></NavLink>
					</li>
					<li>
						<NavLink to="/flower"></NavLink>
					</li>
					<li>
						<NavLink to="/eat"></NavLink>
					</li>
					<li>
						<NavLink to="/dress"></NavLink>
					</li>
					<li>
						<NavLink to="/jewelry"></NavLink>
					</li>
				</ul>
			</div>
		</div>
	</header>
	
	<div className="products">
	<div className="products_content">
		<div className='btnbox'>
			<span><Icon type="environment" /><Icon type="right" />{this.state.location}</span>
		</div>
		<div className="imgbox">
			<img src={this.state.img} alt=''/>
		</div>
		<div className="detailbox">
			<h2>{this.state.location}</h2>
			<p>{this.state.descriptions}</p>
			<div className='detail'>
				<span>
					 <span> &nbsp;{this.state.price}</span>
				</span>
				<span>
					<p>: <span> {this.state.created}</span></p>
					<p>: <span> </span></p>
				</span>
			</div>
			<div className='num'>
			 :  <InputNumber min={1}  defaultValue={1} onChange={this.onChange} />
			 <span className='totalpricebox'>:  <span className='totalprice'>{this.state.totalprice}</span></span>
			</div>
			<NavLink to={{pathname:'/addorder',query:{list:[
				{
					product : this.state.id,
					name : this.state.location,
					coverImg : this.state.img,
					quantity : this.state.count,
					price : this.state.totalprice
				}
			]}}} className='buy'></NavLink>
			<a className='addcart' onClick={this.addCart}></a>
		</div>
	</div>
	</div>
	
	</div>
)
}
}